<template>
  <div class="rank">
    <van-nav-bar
      fixed
      left-text="返回"
      left-arrow
      @click-left="onClickLeft"
    />
    <div v-if="topInfo" class="photo">
      <img :src="topInfo.pic_album" alt="">
    </div>
    <scroll-view class="wrap">
      <van-skeleton
        title
        :row="10"
        :loading="loading"
      >
        <div v-if="topInfo" class="list">
          <div class="list-title">
            <h2>{{topInfo.ListName}}</h2>
            <p>{{topInfo.listennum}}</p>
          </div>
          <div class="list-item" v-for="(e, i) in songList" :key="i" @click="turnOn(i)">
            <p class="order">{{1 + i}}</p>
            <div class="list-info">
              <h3>{{e.data.songname}}</h3>
              <p>{{e.data.singer[0].name}}</p>
            </div>
          </div>
        </div>
        <div v-else class="err">
          加载失败
        </div>
      </van-skeleton>
    </scroll-view>
  </div>
</template>
<script>
import { mapGetters, mapState, mapActions } from 'vuex'
import ScrollView from '#/Common/ScrollView'
export default {
  name: 'RankDetail',
  components: {
    ScrollView,
  },
  data() {
    return {
      rankData: null,
    }
  },
  computed: {
    ...mapState('music', ['loading']),
    ...mapGetters('music', ['songList', 'topInfo'])
  },
  created() {
    this.request()
  },
  methods: {
    ...mapActions('player', ['play']),
    onClickLeft() {
      this.$router.back()
    },
    request() {
      let { id } = this.$route.params
      this.$store.dispatch('music/rankSongs', id)
    },
    turnOn(i) {
      this.play(i)
    },
  }
}
</script>
<style lang="scss" scoped>
/deep/ .van-skeleton__row {
  height: .6rem; }
/deep/ .van-nav-bar {
  background-color: #272727;
  opacity: 0.8; }
/deep/ .van-hairline--bottom::after {
  border: none; }
/deep/ .van-nav-bar__text {
  color: #fff; }
/deep/ .van-nav-bar .van-icon {
  color: #fff; }
.photo {
  z-index: -1;
  position: fixed;
  top: 0;
  width: 100%;
  img {
    width: 100%; } }
.wrap {
  overflow: hidden;
  position: relative;
  top: 40vh;
  height: 60vh;
  .list {
    position: relative;
    border: 1px solid rgb(85, 85, 85);
    background-color: #272727;
    color: #fff;
    &-title {
      h2 {
        padding: .15rem; }
      p {
        font-size: 16px;
        padding: .15rem; }
    }
    &-item {
      position: relative;
      height: 1.1rem;
      font-size: 16px;
      border-bottom: 1px solid rgb(85, 85, 85); }
    .order {
      position: absolute;
      font-size: 16px;
      top: 50%;
      left: .5rem;
      transform: translate(-50%, -50%); }
    &-info {
      position: relative;
      margin-left: 1.1rem;
      h3 {
        padding: .15rem 0; }
      p {
        font-size: 12px; } } } 
  .err {
    position: absolute;
    width: 100%;
    text-align: center; }}


</style>


